<template>
    <div>
        <div class="row">
            <p class="tit">个人信息</p>
        </div>
        <div class="row row1">
            <el-input placeholder="尚未绑定" v-model="email">
                <template slot="prepend">邮箱</template>
                <el-button slot="append" icon="caret-right"></el-button>
            </el-input>
        </div>
        <div class="row row2">
            <el-input placeholder="尚未绑定" v-model="phone">
                <template slot="prepend">手机</template>
                <el-button slot="append" icon="caret-right"></el-button>
            </el-input>
        </div>
        <div class="row">
            <p>我的收藏</p>
        </div>
        <div class="row">
            <p v-show="!likeList">你没有任何收藏</p>
            <el-carousel :interval="4000" type="card" height="200px" v-show="likeList">
                <el-carousel-item v-for="(item,index) in likeList" :key="index">
                    <h3><img :src="item.pic">
                        <span>{{item.name}}</span>
                    </h3>
                </el-carousel-item>
            </el-carousel>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                email: '',
                phone: '',
                likeList: null
            }
        }, mounted() {
            this.axios.post(apiUrl + '/eatApi/user/getLike').then(res => {
                var data = res.data
                if (data.msgcode == 1) {
                    this.likeList = data.likeList
                } else if (data.msgcode == 1014) {
                    this.$message({
                        message: '请先登录',
                        type: 'warning'
                    })
                }
            }).catch(err => {
                console.log(err)
            })
        }
    }

</script>

<style scoped>
    .row1 {
        margin-bottom: 15px;
        margin-top: 15px;
    }

    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 200px;
        margin: 0;
        height: 100%;
    }

    .el-carousel__item span {
        position: absolute;
        bottom: 0;
        width: 100%;
        line-height: 30px;
        height: 30px;
        z-index: 129;
        font-size: 14px;
        text-align: center;
        background: rgba(0, 0, 0, 0.5);
        color: #FFF;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
</style>


